@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

body {
  margin: 0;
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

a {
  color: lightgrey;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:left;padding:20px}.won-card{background-color:#4da375}.lost-card{background-color:#a34d5d}

.MatchDetailsCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;grid-gap:10px;margin-top:20px}.MatchDetailsCard .match-date,.MatchDetailsCard .match-venue,.MatchDetailsCard .match-winner{margin-top:5px}.MatchDetailsCard .additional-details{text-align:right}.MatchDetailsCard .additional-details>p{margin-bottom:10px}

.MatchSmallCard{padding:10px}.MatchSmallCard>*{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;-webkit-justify-content:start;justify-content:start;align-self:center}.TeamPage .team-name{font-size:4rem}.TeamPage .wind-loss-section{justify-self:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center;font-size:1.5rem}.TeamPage .win-loss-section{text-align:center;justify-self:center;padding:30px}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:10px;padding:2px}.YearSelector .currentYear{font-weight:bold}

.MatchPage{display:grid;grid-template-columns:1fr 5fr}.MatchPage .yearSelector{justify-self:center;padding:30px}.MatchPage .yearSelector .back_btn{margin-bottom:30px}.MatchPage .page-heading{margin:20px 0}

.TeamGrid{height:170px;text-align:center;padding:20px;background-color:darkcyan}.TeamGrid:hover{background-color:darkgoldenrod}

.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-row-gap:10px;grid-column-gap:10px}.HomePage .header-section{display:grid;grid-template-columns:1fr 6fr}.HomePage .header-section .app-name{font-size:3rem;margin:30px auto;margin-right:60px;text-align:center}.HomePage .ipl_logo{display:block;width:150px;height:100px;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.HomePage .ipl_logo .ipl_logo{height:100%;width:100%}

