.MatchDetailsCard {

    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 10px;
    grid-gap: 10px;
    margin-top: 20px;

    .match-date, .match-venue, .match-winner {
        margin-top: 5px;
    }

    .additional-details {
        text-align: right;

        & > p {
            margin-bottom: 10px;
        }
    }
}
